<template>
  <div class="login">
    <van-nav-bar
      left-text="首页"
      left-arrow
      @click-left="onClickLeft"
      :border='false'
    />

    <!-- app背景 -->
    <div class="bg">
      <p class="title">寻 宝</p>
      <span class="sub">生活 . 在这得到满足</span>
    </div>

    <!-- 登录框 -->
    <div class="form">
      <!-- 手机号 -->
      <van-field
        v-model="userLoginInfo.phone"
        type="tel"
        label="手机"
        placeholder="请输入手机号"
      />
      <!-- 密码 -->
      <van-field
        v-model="userLoginInfo.password"
        :type="isType ? 'text' : 'password'"
        label="密码"
        placeholder="请输入密码"
        :right-icon="isType ? 'eye-o' : 'closed-eye'"
        @click-right-icon="toggleType"
      />

      <!-- 注册/忘记密码 -->
      <div class="sub">
        <span class="reg" @click="isShow">注 册</span>
        <span class="psw">忘记密码?</span>
      </div>

      <!-- 登录按钮 -->
      <div class="btn">
        <van-button
          round
          block
          color="linear-gradient(to right,#ff9000 0,#ff5000 100%)"
          @click="login"
          >登 录</van-button
        >
      </div>
    </div>

    <!-- 注册框 -->
    <van-popup
      v-model="show"
      closeable
      close-icon="close"
      position="bottom"
      round
      class="popup"
    >
      <div class="register">
        <!-- 手机号码 -->
        <van-field
          v-model="userRegisterInfo.phone"
          type="tel"
          label="手机"
          placeholder="请输入手机号"
        />
        <!-- 密码 -->
        <van-field
          v-model="userRegisterInfo.password"
          :type="isType ? 'text' : 'password'"
          label="密码"
          placeholder="请输入密码"
          :right-icon="isType ? 'eye-o' : 'closed-eye'"
          @click-right-icon="toggleType"
        />
        <!-- 验证码 -->
        <van-field
          v-model="userRegisterInfo.code"
          center
          clearable
          label="验证码"
          placeholder="请输入短信验证码"
        >
          <template #button>
            <van-button
              size="small"
              round
              color="linear-gradient(to right,#ff9000 0,#ff5000 100%)"
              @click="getcode"
              >发送验证码</van-button
            >
          </template>
        </van-field>
      </div>

      <div class="btn">
        <van-button
          round
          block
          color="linear-gradient(to right,#ff9000 0,#ff5000 100%)"
          @click="register"
          >注 册</van-button
        >
      </div>
    </van-popup>
  </div>
</template>

<script>
import "../assets/style/Login.less";
import { utils } from "../assets/js/utils";
export default {
  name: "login",
  data() {
    return {
      //是否显示弹出层
      show: false,

      //用户登录信息
      userLoginInfo: {
        phone: "",
        password: "",
      },

      //用户注册信息
      userRegisterInfo: {
        phone: "",
        password: "",
        code: "",
      },

      //验证码需要的key
      codekey: "",

      // 密码是否可见
      isType: false,
    };
  },
  methods: {
    onClickLeft() {
      console.log('返回首页');
      this.$router.push("/")
    },

    isShow() {
      //点击注册显示注册框
      this.show = true;

      // 获取验证码需要的key
      this.axios(this.baseUrl + "/verify_code").then((data) => {
        this.codekey = data.data.data.key;
        console.log(this.codekey);
      });
    },

    //获取验证码
    getcode() {
      this.axios({
        method: "POST",
        url: this.baseUrl + "/register/verify",
        data: {
          code: "",
          key: this.codekey,
          phone: this.userRegisterInfo.phone,
          type: "register",
        },
      })
        .then((data) => {
          console.log(data);
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    //注册
    register() {
      console.log();
      let reg = {
        phone: {
          value: this.userRegisterInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机号格式不正确",
        },

        password: {
          value: this.userRegisterInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "密码只支持字母数字且以字母开头",
        },
      };

      //验证表单
      //表单验证不通过, 拦截注册请求
      if (!utils.validForm(reg)) {
        return;
      }

      //发起注册请求
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "POST",
        url: this.baseUrl + "/register",

        //如果是POST请求，参数需要挂载到data
        data: {
          account: this.userRegisterInfo.phone,
          password: this.userRegisterInfo.password,
          captcha: this.userRegisterInfo.code,
          spread: null,
        },
      })
        .then((res) => {
          console.log("res ==> ", res);
          this.$toast.clear();
          if (res.data.status == 200) {
            //关闭注册弹出层
            this.isOpen = false;
            //清除用户注册信息
            for (let key in this.userRegisterInfo) {
              this.userRegisterInfo[key] = "";
            }
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    //登录
    login() {
      let log = {
        phone: {
          value: this.userLoginInfo.phone,
          reg: /^1[3-9]\d{9}$/,
          errorMsg: "手机号格式不正确",
        },
        password: {
          value: this.userLoginInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          errorMsg: "密码只支持字母数字且以字母开头",
        },
      };

      //验证表单
      //表单验证不通过, 拦截注册请求
      if (!utils.validForm(log)) {
        return;
      }

      //发起登录请求
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "POST",
        url: this.baseUrl + "/login",

        //如果是POST请求，参数需要挂载到data
        //a=1&b=2&c=3
        data: {
          account: this.userLoginInfo.phone,
          code: "",
          password: this.userLoginInfo.password,
        },
      })
        .then((res) => {
          console.log("res login ==> ", res);
          this.$toast.clear();

          if (res.data.status === 200) {
            //登录成功
            //存储token, 用于下次访问有关于用户信息界面的登录标记
            this.$cookies.set("tokenString", res.data.data.token, "1d");

            //跳转到其他页面
            this.$router.back();
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    //切换密码框的类型
    toggleType() {
      this.isType = !this.isType;
    },
  },
};
</script>

<style lang="less" scoped>
</style>